<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml"
          xmlns:v-if="http://www.w3.org/1999/xhtml">

    <div class="signIn" id="sign">
        {{msg}}
        <div id="insideSign">
            <h1>SIGN UP</h1>
        <div class="notice">they are all have to be written in english</div>
            <br>
        <div class="title">Full name</div>
        <input type="text" v-model = "Name" class = "inputText" style="width: 100%; height: 30px;" required/>
        <br>
            <div class="title">User name</div>
        <input type="text" id="id" v-model="Id" style="width: 70%; height: 30px;" required/>
        <button v-on:click="checkID" id="validID">check Invalid ID</button>
        <img v-if="invalidID" class = "checkIMG" src='../../public/images/check-mark-icon-png.jpg'>
        <!--db 에서 가져온 ID 정보로 겹치는 아이디가 있는지 확인하는 방법-->
        <br>
            <div class="title">Password</div>
            <input type="text" v-model="Password"
               id="password" style="width: 100%; height: 30px;" required/>
        <div class="notice">must contain 8 or more characters that are of at least one number, and one
        uppercase and lowercase letter</div>
            <br>
            <div class="title">Email</div>
            <input type="text" v-model="Email" id="email" style="width: 100%; height: 30px;" required/>
        <div class="notice" v-if="hideEN">email has to be ex)XXXX@XXXX.XXX </div>
            <div class="notice" v-if="!hideEN" id="emailNotice">email has to be ex)XXXX@XXXX.XXX</div>
             <div class="title">University & Major</div>
            <select v-model="University" style="width: 50%; height: 30px"></select>
            <div v-for="uname in uniname" :key="uname" id="university">
                <option value="uname">{{uname}}</option>
            </div>
            <select v-model="UniversityIndex" style="width: 50%; height: 30px"></select>
            <select v-model="Major" style="width: 50%; height: 30px;"></select>
        </div>
        <button v-on:click="btnClicked" class="inputText" id="submit">Register</button>

    </div>
</template>

<script>
    import axios from 'axios'
    const instance = axios.create({
        baseURL: 'http://localhost:3000',
        timeout: 1000,
        headers: {'X-Custom-Header': 'foobar'}
    });

    export default{
        name: 'singIn',
        props:{
            msg:String
        },
        data(){
            return{
                validationOfID : true,
                Email:'',
                invalidCODE :true,
                hideEN:true,
                university:[{
                    name:'',
                    nameindex:'',
                    major:''
                }],
                uniname:[],
                user: {
                    id:'',
                    name:'',
                    password:'',
                    university:'',
                    major:'',
                    email:'',
                    country:''
                }
            }
        },
        created: function(){
            instance.get('/api/universityList/name')
                .then((response) => {
                    this.uniname = response.data;
                })
        },
        methods:{
            //"(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"

            btnClicked(){
                var checkindex = 0;
                for(var i = 0; i<this.Email.length; i++) {
                    if (this.Email.includes('@')) {
                        checkindex ++;
                    }
                }
                if(checkindex !== 1) {
                    this.Email = '';
                    this.hideEN = false;
                }
            }
            ,



        },
        computed:{

        }

    }
</script>
<style scoped>
    .notice{
        margin-top:10px;
        text-decoration-color: darkred;
        margin-bottom:20px;
    }
    #id{
        clear:both;
        margin-bottom:10px;
    }
    #validID{
        clear:left;
        margin-left:10px;
        background-color: #ffc952;
        border :none;
        color:white;
        text-decoration:none;
        cursor: pointer;
        height: 30px;
    }

    .inputText{
        margin-bottom: 20px;
        height: 30px;
    }
    #sign{
        border:3px solid #ffc952;
        height: auto;
        width:500px;
        align-content: start;
        margin: auto;
    }
    #insideSign{
        text-align: left;
        padding: 10px;
    }
    #password{

    }
    #submit{
        margin-top: 15px;
        background-color: #ffc952;
        border :none;
        color:white;
        text-align: right;
        text-decoration:none;
        cursor: pointer;
        height: 30px;

    }
    #emailNotice{
        text-decoration-color: darkred;
        text-emphasis-color: darkred;
    }

</style>
